$breakpoint-xs-margin: 16px;
$breakpoint-xs-width: 0;
$breakpoint-md-margin: 24px;
$breakpoint-md-width: 672px;
$breakpoint-lg-margin: 24px;
$breakpoint-lg-width: 1056px;
$color-blue-deep: #2d2938;
$color-blue-dark: #2c3357;
$color-blue-dark-light: #c9cde4;
$color-blue-sky: #4c629e;
$color-blue-primary: #0272d5;
$color-blue-primary-light: #aed9fe;
$color-red: #dc4b43;
$color-red-light: #f2bdba;
$color-yellow: #ffb337;
$color-yellow-light: #ffe0ad;
$color-green: #0ca76b;
$color-green-light: #b3fade;
$color-grey-0: #ecebec;
$color-grey-35: #bfbbbd;
$color-grey-60: #918a8d;
$color-grey-85: #62595d;
$color-grey-100: #473c41;
$color-white: #ffffff;
$color-black: #000000;
$font-family: CentraNube, -apple-system, BlinkMacSystemFont, Roboto,
  "Helvetica Neue", arial, sans-serif;
$font-weight-regular: 400;
$font-weight-bold: 700;
$font-size-x-small: 12px;
$font-size-small: 14px;
$font-size-medium: 16px;
$font-size-large: 18px;
$font-size-x-large: 20px;
$font-size-xx-large: 24px;
$font-size-xxx-large: 32px;
$font-line-height-base: 1;
$font-line-height-heading: 1.25;
$font-line-height-body: 1.5;
$motion-fast: 150ms;
$motion-base: 300ms;
$motion-slow: 450ms;
$motion-slower: 600ms;
$spacing-xx-small: 4px;
$spacing-x-small: 8px;
$spacing-small: 16px;
$spacing-medium: 24px;
$spacing-large: 32px;
$spacing-x-large: 40px;
$spacing-xx-large: 48px;
$spacing-xxx-large: 56px;
$spacing-gutter: 32px;


$color-primary: #98ECFF;
$color-secondary: #111424;

html,
body {
  padding: 0;
  margin: 0;
  font-family: $font-family;
  background: $color-secondary;
}

body {
  background: url(../images/icon-grid.svg) top 129px right -15px no-repeat,
              url(../images/horizontal-grid.svg) top 105px right -15px repeat-x,
              url(../images/vertical-grid.svg) top right -147px repeat-y;
}

a {
  color: inherit;
  text-decoration: none;
}
  
* {
  box-sizing: border-box;
}

/* Texts */

h1,
h2,
h3,
h4,
h5,
h6 {
  color: $color-primary;
}

h2{
  font-size: 42px;
}

p {
  font-size: 16px;
  color: white;
}

.text-primary {
  color: $color-primary;
  -webkit-text-stroke-color: $color-primary;
}

.text-secondary {
  color: $color-secondary;
}

.outline{
}

.logo{
}

.font-xs {
  font-size: $font-size-x-small;
}

.font-s {
  font-size: $font-size-small;
}

.font-m {
  font-size: $font-size-medium;
}

.font-bold {
  font-weight: bold;
}

.text-outline {
  color: transparent;
  -webkit-text-stroke-width: 2px;
}

/* Buttons */

.btn {
  border-radius: 8px;
  padding: 10px;

}
.btn-primary {
  background-color: $color-grey-0;
}

.btn-secondary {
  background-color: $color-secondary;
  color: white;
}

.btn-link {
  text-decoration: underline;
  color: $color-primary;
}

/* Header */

.head {

}

/* Hero */

.hero {
}

.hero-title {
  font-size: 80px;
  color: $color-primary;
}

@media (min-width: 576px) {
	.hero-title {
  		font-size: 112px;
	}
}

/* SVG Icons */

.svg-icon-secondary{
  fill: $color-secondary;
  height: 24px;
  width: 24px;
  margin-right: 8px;
 }

/* Modal */

.modal{
  display: none;
  background: $color-grey-0;
  border-radius: 8px;
  padding: 12px;
}

.modal-body{
  padding: 12px;
  color: $color-secondary;
}

.modal-icon{
  fill: $color-grey-0;;
}

.icon-gallery {
  margin-top: $spacing-large;
}

.icon-gallery-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
  margin-left: -$spacing-large;
  margin-top: -$spacing-medium;
}

.icon-gallery-item {
  cursor: pointer;
  margin-left: $spacing-small;
  margin-top: $spacing-medium;
  margin-right: $spacing-small;
  padding: $spacing-medium;
  transition: $motion-fast ease;
  border: 1px solid transparent;
  border-radius: $spacing-xx-small;
}

.icon-gallery-item:hover {
  border-color: rgba($color-primary, .2);
}

.icon-gallery-item svg {
  fill: $color-primary;
  transition: $motion-fast ease;
}

/* Footer */

footer {
  width: 100%;
  margin-top: $spacing-xxx-large;
  background: $color-grey-0;
} 

.social-media-logo {
  padding: 15px 15px 15px 0;
}

a+a {
  margin-left: 5px;
}

li {
  text-decoration: none;
  padding: 5px;
  list-style: none;
}


